<template>
  <div class="content">
    <section class="formDiv">
      <form action="#">
        <div class="formLine">
          <p>姓名：</p>
          <input type="text" name id />
        </div>
        <div class="formLine">
          <p>性别：</p>
          <input type="text" name id />
        </div>
        <div class="formLine">
          <p>手机号：</p>
          <input type="text" name id />
        </div>
        <div class="formLine">
          <p>身份证：</p>
          <input type="text" name id />
        </div>
        <div class="formLine" @click="openPickerTime">
          <p>出生日期：</p>
          <input type="text" :value="currentDateTime" name id readonly />
        </div>
        <div class="formLine">
          <p>联系地址：</p>
          <input type="text" name id />
        </div>
        <div class="formLine" @click="openPickerTime">
          <p>开户日期：</p>
          <input type="text" :value="currentDateTime" name id readonly />
        </div>
        <div class="formLine" @click="openPickerTime">
          <p>有效期开始：</p>
          <input type="text" :value="currentDateTime" name id readonly />
        </div>
        <div class="formLine" @click="openPickerTime">
          <p>有效期结束：</p>
          <input type="text" :value="currentDateTime" name id readonly />
        </div>
        <div class="formLine">
          <p>备注：</p>
          <textarea cols="10" rows="5"></textarea>
        </div>
        <div class="formLine">
          <p>业绩人：</p>
          <input type="text" name id />
        </div>
      </form>
      <div class="btnLine">
        <div @click="$router.push({path:'/selectVipCard'})">确认提交</div>
      </div>
    </section>
    <section class="pickerDataTime">
      <van-overlay :show="show" @click="openPickerActive = false ; show = false" />
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        :min-date="minDate"
        :max-date="maxDate"
        v-show="openPickerActive"
        @cancel="openPickerActive = false ; show = false"
        @confirm="confirmTime"
      />
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      openPickerActive: false,
      minDate: new Date(1950, 1, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      currentDateTime: this.util.getTime(new Date()).substring(0,10),
      show: false
    };
  },
  methods: {
    openPickerTime() {
      this.openPickerActive = !this.openPickerActive;
      this.show = !this.show;
    },
    confirmTime(value) {
      this.currentDateTime = this.util.getTime(value);
      this.openPickerActive = !this.openPickerActive;
      this.show = !this.show;
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  // padding: 0.8rem ;
  .formDiv {
    background-color: $fc;
    border-radius: 0.5rem;
    padding: 0.5rem;
    z-index: 2;
    position: relative;
    .formLine {
      @include fr(flex-start);
      margin: 0.8rem 0rem;
      p:first-child {
        @include wh(6.5rem, auto);
        @include sc(0.9rem, $fColor);
        text-align: right;
      }
      input {
        border-radius: 0.2rem;
        border: 0.05rem solid $F5;
        padding: 0.2rem 0.3rem;
        flex: 1;
      }
      textarea {
        border-radius: 0.2rem;
        border: 0.05rem solid $F5;
        flex: 1;
        padding: 0.2rem 0.3rem;
      }
      .right {
        @include wh(100%, auto);
      }
      .rightLine {
        @include fr(space-between);
        @include wh(100%, auto);
        margin: 0.5rem 0rem;
        padding: 0.5rem;
        border: 0.05rem solid $F5;
        border-radius: 0.5rem;
        @include sc(0.85rem, $fColor);
      }
    }
    .btnLine {
      @include fr(space-between);
      div {
        padding: 0.5rem;
        border-radius: 0.5rem;
        margin: 1.5rem 0.5rem 1rem 0.5rem;
        @include sc(0.9rem, $fc);
        flex: 1;
      }
      div:first-child {
        background-color: #01cfee;
        background-image: linear-gradient(
          242deg,
          #01cfee 0%,
          #15b8f1 50%,
          #3795f8 100%
        );
      }

      div:last-child {
        background-color: #21d4fd;
        background-image: linear-gradient(225deg, #21d4fd 0%, #b721ff 100%);
      }
    }
  }
  .pickerDataTime {
    position: fixed;
    bottom: 0;
    z-index: 2;
    @include wh(100%, auto);
  }
}
/deep/ .van-datetime-picker {
  z-index: 2;
}
</style>